import React from 'react'
import css from './footer.css'

class Footer extends React.Component {
    render () {
      return <div className={css.footer}>
        <div>
          logo
        </div>
        <div>
          location
          <i class='bx bx-phone' style='color:rgba(0,0,0,0.19)'  ></i>
          <a>email</a>
        </div>
        <div>
          rights reserved
        </div>
      </div>
    }
  }

  export default Footer